@use 'base/variables' as v;
@use 'base/mixins' as m;

.dashboard {
    background-color: v.$grisClaro;

    @include m.tablet {
        display: flex;
        height: 100vh;
    }

    .principal {
        flex: 1;
        @include m.tablet {
            overflow-y: scroll;
        }
        
    }

    

    .contenido {
        padding: 2rem;
        .nombre-pagina {
            text-align: center;
        }
    }

    .formulario {
        input:not([type="submit"]) {
            background-color: v.$blanco;
        }

        input[type="submit"] {
            @include m.boton(v.$indigo, v.$blanco);
            text-transform: uppercase;
            font-weight: bold;
        }
    }

    .enlace {
        color: v.$indigo;

        &:hover {
            color: darken(v.$indigo, 20%);
        }
    }


}